import React from "react";
import { Link } from "react-router-dom";
import Grid from "./Grid";
import logo from "../assets/images/Logo-2.png";

const footerAboutLinks = [
  {
    display: "Gii thiu",
    path: "/contact",
  },
  {
    display: "Lin h",
    path: "/contact",
  },
  {
    display: "Tuyn dng",
    path: "/contact",
  },
  {
    display: "Tin Tc",
    path: "/contact",
  },
  {
    display: "H thng ca hng",
    path: "/contact",
  },
];

const footerCustomerLinks = [
  {
    display: "Chnh sch i tr",
    path: "/contact",
  },
  {
    display: "Chnh sch bo hnh",
    path: "/contact",
  },
  {
    display: "Chnh sch hon tin",
    path: "/contact",
  },
];

const Footer = () => {
  return (
    <footer className="footer">
      <div className="container">
        <Grid col={4} mdCol={2} smCol={1} gap={10}>
          <div>
            <div className="footer__title">Tng i h tr</div>
            <div className="footer__content">
              <p>
                Lin h t hng <strong>0818890057</strong>
              </p>
              <p>
                Thc mc n hng <strong>0818890057</strong>
              </p>
              <p>
                Gp , khiu ni <strong>0818890057</strong>
              </p>
            </div>
          </div>
          <div>
            <div className="footer__title">V Yolo</div>
            <div className="footer__content">
              {footerAboutLinks.map((e, i) => (
                <p key={i}>
                  <Link to={e.path}>{e.display}</Link>
                </p>
              ))}
            </div>
          </div>
          <div>
            <div className="footer__title">Chm sc khch hng</div>
            <div className="footer__content">
              {footerCustomerLinks.map((e, i) => (
                <p key={i}>
                  <Link to={e.path}>{e.display}</Link>
                </p>
              ))}
            </div>
          </div>
          <div>
            <p>
              <Link to="/">
                <img src={logo} className="footer__logo" alt="" />
              </Link>
            </p>
            <p>
              Hng n mc tiu mang li nim vui n mc mi mi ngy cho hng
              triu ngi tiu dng Vit. Hy cng Yolo hng n mc tiu mt
              cuc sng nng ng, tch cc hn.
            </p>
          </div>
        </Grid>
      </div>
    </footer>
  );
};

export default Footer;
